        this.redrawPet = function(){
            _self.hide();
            _self.draw();
            _self.setEvents(true);
        };
        
        this.setEvents = function(petOnly){
            if(!window.catpet.isPlayground){
                // SET DRAGGING EVENTS
                setDraggable();
                
                // ON MOUSE OVER - PETTING 
                document.getElementById(window.catpet["pet-holder"] + _self.id).addEventListener("mouseover", function (e) {
                    _self.startPettingTimeout = setTimeout(function(){
                        _self.pet();
                    }, 1500);
                });
                
                document.getElementById(window.catpet["pet-holder"] + _self.id).addEventListener("mouseout", function (e) {
                    if(_self.startPettingTimeout){
                        clearTimeout(_self.startPettingTimeout);
                        _self.startPettingTimeout = null;
                    }
                    if(_self.animationName == "petting"){
                        _self.idle({initiator: "Petting done"});
                    }
                    
                });
            }
            
            if(!petOnly && !window.catpet.isPlayground){         
                // In same cases though, site will stop bubbling and won't allow me to stop default behaviour
                // Biggest example is gmail, so here we listen to everything and we use Capture option instead of Bubbling
                // And then we check if it should be disabled based on what we clicked;
                document.addEventListener('contextmenu', e => {
                    var prevent = false;
                    for(var i = 0; i < e.path.length; i++){
                        if(e.path[i].id == window.catpet["external-holder"] + _self.id || e.path[i].id == window.catpet["bubble-holder"] + _self.id || e.path[i].id == "catpet-menu"){
                            prevent = true;
                            break;
                        }
                    }
                    if(prevent){
                        e.preventDefault()
                    }
                }, true);
                
               
                
            }
            // ON MOUSE DOWN - OPEN CONTEXT MENU
            if(!window.catpet.isPlayground){
                document.getElementById(window.catpet["pet-holder"] + _self.id).addEventListener("mouseup", function (e) {
                    if (e.which == 3){
                        _self.contextMenuToggle(e);
                        e.preventDefault();
                        e.stopPropagation();
                        return false;
                    } else {
                        window.catpet.killMeowContextMenu();
                    }
                });
                // FOR MOST SITES, THIS IS ENOUGH, IT WILL STOP THE DEFAULT BEHAVIOUR
                document.getElementById(window.catpet["pet-holder"] + _self.id).addEventListener('contextmenu', e => {e.preventDefault()});
            }
            
        }
        
        this.loadDataFromLocalStorage = function(data){
            //window.catpet.log("loading data ", data);
            try{
                this.stats.posLeft = data.posLeft;
                this.stats.posTop = data.posTop;
                this.stats.hideUntil = data.hideUntil;
                this.stats.walk = data.walk;
                if((typeof data.walkType == "undefined" && typeof data.walk != "undefined" && data.walk == true) || data.walkType == "WALK")
                    this.stats.walkType = "WALK";
                else if ((data.walkType == "EDGES" || data.walkType == "BOTTOM"))
                    this.stats.walkType = data.walkType;
                else
                    this.stats.walkType = "FALSE";
                
                if(this.stats.walkType == "BOTTOM")
                    this.stats.posTop = window.catpet.viewport().height;
                this.stats.talk = data.talk;
                if(data.idleType)
                    this.stats.idleType = data.idleType;
                if(data.skin)
                    this.stats.skin = data.skin;
                if(data.hat)
                    this.stats.hat = data.hat;
                if(data.eyes)
                    this.stats.eyes = data.eyes;
            } catch(e){
                window.catpet.log("Failed loading data", e);
            }
        };
        
        this.drawPositions = function(){
            //console.log("Draw position", this.stats.posLeft, this.stats.posTop);
            if(window.catpet.isPlayground){
                _self.rendererContainer.position.x = this.stats.posLeft;
                _self.rendererContainer.position.y = this.stats.posTop;
                if(this.catIsMine){
                    //window.RenderManager.pgWorld.stage.pivot.copy(_self.rendererContainer.positions);
                    //window.RenderManager.pgWorld.stage.position.set(_self.rendererContainer.position.x, _self.rendererContainer.position.y/2);
                }
            } else {
                _self.rendererContainer.position.x = this.browserPetData.width / 2;
                _self.rendererContainer.position.y = this.browserPetData.height;
                //document.getElementById(window.catpet["external-holder"] + _self.id).style.left = (this.stats.posLeft - (this.browserPetData.width / 2)) + "px";
                this.browserPetData.holderElement.style.left = (this.stats.posLeft - (this.browserPetData.width / 2)) + "px";
                //document.getElementById(window.catpet["external-holder"] + _self.id).style.top  = (this.stats.posTop - this.browserPetData.height) + "px";
                this.browserPetData.holderElement.style.top  = (this.stats.posTop - this.browserPetData.height) + "px";
            }
        }
        this.fixPositions = function(returnValues, data){
            if(!returnValues){
                data  = {
                    x: this.stats.posLeft,
                    y: this.stats.posTop
                }
            }
            
            // too much right side
            if(window.catpet.viewport().width < (data.x + Math.abs(_self.rendererPet.width / 2))){
                data.x = window.catpet.viewport().width - Math.abs(_self.rendererPet.width / 2);
            }
            // too much left side
            else if(data.x - Math.abs(_self.rendererPet.width / 2) < 0){
                data.x = Math.abs(_self.rendererPet.width / 2);
            }
            
            // too high
            if(data.y - (_self.rendererPet.height) < 0){
                data.y = (_self.rendererPet.height);
            }
            // too low
            else if(window.catpet.viewport().height < (data.y ) ){
                data.y = window.catpet.viewport().height;
            }
            else if(_self.stats.walkType == "BOTTOM"){
                 data.y = window.catpet.viewport().height;
            }
            else if(_self.stats.walkType == "EDGES" 
            && _self.stats.posTop > this.browserPetData.height // not stick to top
            && _self.stats.posTop < window.catpet.viewport().height // not stick to bottom
            && _self.stats.posLeft > (this.browserPetData.width/2) // not stick to left
            && _self.stats.posLeft < window.catpet.viewport().width - (this.browserPetData.width/2) // not stick to right
            ){
                 data.y = window.catpet.viewport().height;
            }
            
            if(returnValues){
                return data;
            } else {
                this.stats.posLeft = data.x;
                this.stats.posTop = data.y;
            }
        }
                        
        this.reposition = function(changeHTML, updateBG, reportToServer){
            if(typeof reportToServer === "undefined")
                reportToServer = false;
                
            if(!_self.rendererPet)
                return;
                
            if(this.currentMovingActionInterval)
                _self.idle({
                    report: reportToServer
                });
            
            this.fixPositions();            
            
            if(changeHTML && window.catpet && window.catpet.isPetActive(_self.id)){
                if(_self.rendererPet){
                    _self.drawPositions();
                }
            }
            
            if(updateBG && !window.catpet.isPlayground){
                _self.sendPositionsToBG();
            }
        };
        
