        this.sayHTML = function(text, encodedText){ 
            var bubble = document.getElementById(window.catpet["bubble-holder"]+_self.id);
            var bubbleText = document.getElementById(window.catpet["bubble-holder-text"]+_self.id);
            if(!bubble || !bubbleText){
                return;
            }
            bubble.style["z-index"] = window.catpet.currentBubbleIndex++;
            //document.getElementById(window.catpet["external-holder"] + _self.id).style["z-index"] = window.catpet.currentBubbleIndex;
            bubble.style.display = "flex";
            
            let marginFromLeft = 105;
            
            bubble.style.left = marginFromLeft + "px";
            bubble.style.top = -140 + "px";
            
            bubble.style.background = "url("+window.catpet.getURL('/images/bubble.svg')+")";
            bubble.className = "catpet-bubble";
            
            // Left? //
            if(window.catpet.viewport().width - window.catpet.bubbleWidth - marginFromLeft <= this.stats.posLeft - (this.browserPetData.width / 2)
            && window.catpet.bubbleHeight -70 <= this.stats.posTop - (this.browserPetData.height)
            ){
                bubble.className = "catpet-bubble l";
                bubble.style.background = "url("+window.catpet.getURL('/images/bubble-l.svg')+")";
                bubble.style.left = (-window.catpet.bubbleWidth + 10) + "px";
                bubble.style.top = -140 + "px";                
            } 
            // Bottom Left?
            else if(window.catpet.viewport().width - window.catpet.bubbleWidth - marginFromLeft <= this.stats.posLeft - (this.browserPetData.width / 2)
                    && window.catpet.bubbleHeight -70 > this.stats.posTop - (this.browserPetData.height)){
                bubble.className = "catpet-bubble bl";
                bubble.style.background = "url("+window.catpet.getURL('/images/bubble-bl.svg')+")";
                bubble.style.left = (-window.catpet.bubbleWidth + 10) + "px";
                bubble.style.top = 70 + "px";
            } 
            // Bottom right?
            else if(window.catpet.bubbleHeight - 70 > this.stats.posTop - (this.browserPetData.height)) {
            //else if(window.catpet.viewport().height - window.catpet.bubbleHeight - 128 <= parseInt(document.getElementById(window.catpet["external-holder"]).style.top)){
                bubble.className = "catpet-bubble br";
                bubble.style.background = "url("+window.catpet.getURL('/images/bubble-br.svg')+")";
                bubble.style.left = marginFromLeft + "px";
                bubble.style.top = 70 + "px";
            } 
            
            bubbleText.innerHTML = text;
            if(this.clearBubbleTimeout){
                clearTimeout(this.clearBubbleTimeout);
            }
            this.clearBubbleTimeout = setTimeout(function(){
                bubble.style.display = "none";
                //bubbleText.innerHTML = "";   
            }, 5000);

        };
        
        
        this.say = function(text, encodedText){
            if(!window.catpet.isPlayground){
                this.sayHTML(text, encodedText);
                return;
            }
            
            
            this.dismissBubble();
            _self.currentBubble =  new PIXI.Container(); 
            _self.currentBubble.parentLayer = window.RenderManager.layer;
            _self.currentBubble.zOrder = ++window.RenderManager.currzIndex + 10000;
            //_self.currentBubble.zOrder = _self.currentBubble.zIndex;
            //_self.rendererContainer.zIndex = ++window.RenderManager.currzIndex;
            
            //_self.organizezIndex();
            
            
            var txt = new PIXI.Text(text,{
                fontSize: 16,
                fill: "#000",
                align: 'center',
                fontFamily : 'arial',
                //fontWeight: 'bold',
                //strokeThickness: 1,
                wordWrap: true,
                wordWrapWidth: window.catpet.bubbleWidth - 30,
                breakWords: true
            });
            //txt.resolution = 4;
            txt.x = window.catpet.bubbleWidth / 2;
            txt.y = window.catpet.bubbleHeight / 2 - 20;
            txt.anchor.set(0.5);            
            
            
            // Left?
            if(
            (
                (window.catpet.viewport().width - window.catpet.bubbleWidth  - 60  <= this.stats.posLeft)
                ||
                (_self.stats.direction == "left" && (this.stats.posLeft - window.catpet.bubbleWidth  - 60  > 0))
            )
            && window.catpet.bubbleHeight  <= this.stats.posTop - _self.rendererContainer.height + 60
            ){
                var bubble = PIXI.Sprite.from('/images/bubble-l.png');
                bubble.width = window.catpet.bubbleWidth;
                bubble.height = window.catpet.bubbleHeight;
                
                _self.currentBubble.y = (-1 * window.catpet.bubbleHeight - _self.rendererContainer.height) + 60;
                _self.currentBubble.x = -window.catpet.bubbleWidth - 60;                
            } 
            // Bottom Left?
            else if(
                (
                    (window.catpet.viewport().width - window.catpet.bubbleWidth  - 60  <= this.stats.posLeft)
                    ||
                    (_self.stats.direction == "left" && (this.stats.posLeft - window.catpet.bubbleWidth  - 60  > 0))
                )
                    && window.catpet.bubbleHeight  > this.stats.posTop - _self.rendererContainer.height + 60){
                var bubble = PIXI.Sprite.from('/images/bubble-bl.png');
                bubble.width = window.catpet.bubbleWidth;
                bubble.height = window.catpet.bubbleHeight;
                _self.currentBubble.y = (-1 * _self.rendererContainer.height) + 70;
                _self.currentBubble.x = -window.catpet.bubbleWidth - 60;
                txt.y = window.catpet.bubbleHeight / 2 + 20;
            } 
            // Bottom right?
            else if(window.catpet.bubbleHeight > this.stats.posTop - _self.rendererContainer.height + 60) {
                
                var bubble = PIXI.Sprite.from('/images/bubble-br.png');
                bubble.width = window.catpet.bubbleWidth;
                bubble.height = window.catpet.bubbleHeight;
                _self.currentBubble.y = (-1 * _self.rendererContainer.height) + 70;
                _self.currentBubble.x = _self.rendererContainer.width - 60;
                txt.y = window.catpet.bubbleHeight / 2 + 20;
            } else {
                var bubble = PIXI.Sprite.from('/images/bubble.png');
                bubble.width = window.catpet.bubbleWidth;
                bubble.height = window.catpet.bubbleHeight;
                
                _self.currentBubble.y = (-1 * window.catpet.bubbleHeight - _self.rendererContainer.height) + 60;
                _self.currentBubble.x = _self.rendererContainer.width - 60;                 
            }
            /*
            bubbleText.innerHTML = text;
            
            */

            _self.currentBubble.addChild(bubble, txt);
            _self.currentBubble.alpha = 0.90;
            _self.rendererContainer.addChild(_self.currentBubble);
            // Create tinmeout to clean current bubble
            this.clearBubbleTimeout = setTimeout(function(){
                _self.currentBubble.parent.removeChild(_self.currentBubble);
                _self.clearBubbleTimeout = null;
                _self.currentBubble = null;
                //bubble.style.display = "none";
                //bubbleText.innerHTML = "";   
            }, 5000);
        };
        
        this.dismissBubble = function(){
            // Clean timeout so we can extend the display time
            if(_self.clearBubbleTimeout){
                clearTimeout(this.clearBubbleTimeout);
                _self.clearBubbleTimeout = null;
            }
            if(_self.currentBubble){
                _self.currentBubble.parent.removeChild(_self.currentBubble);
                _self.currentBubble = null;
            }
        };
        
        this.isHurt = function(isTag = false){
            _self.clearAllActions();
            
            // This happens when the target already received a response that he got hit, now lets make sure the server stops moving.
            // This In case we sent a movement before we received the got hit but the server received the got hit first
            if(_self.catIsMine){
                
                window.catpetpg.socket.emit("hit_confirm");
            }
                
            if(window.catpet.isPetActive(_self.id)){
                
                if(_self.stats.direction == "left")
                    _self.rendererPet.scale.x = -1 * _self.scale;
                else
                    _self.rendererPet.scale.x = _self.scale; 
            }
            _self.changeAnimation(0, "hurt", false, 0);
            //_self.rendererPet.state.addAnimation(0, "sad", false, 0);
            
            // Only if we were idle, turn us into sad
            // If we're in tag, we receive the last hit event AFTER we send event to reset everyone
            // So if we received TAG right after getting hit, we should not become sad
            // Therefore, in tag, make us sad only if we are not white skin
            if(_self.stats.idleType == "idle" 
            //&& (!isTag || (isTag && _self.stats.skin != "white") )
            )
                _self.stats.idleType = "sad";
                
            _self.currentAnimationTimeout = setTimeout(function(){
                _self.idle({
                    type: _self.stats.idleType,
                    report: (window.catpet.isPlayground && _self.catIsMine)
                });
            }, 1500);
        };        
        
        this.pet = function(){
            if(_self.stats.idleType == "sleep") { 
                return;
            }
            this.clearAllActions();
            _self.animationName = "pet";
            
            if(window.catpet.isPetActive(_self.id)){
                _self.changeAnimation(0, "petting", true, 0);
            }
                        
            _self.pettingRewardInterval = setInterval(function(){
                window.catpet.sendToBackground("petting")
            }, 1000);
            
        };
        
