        this.petOther = function(target){
            if(_self.animationName == "hurt" || _self.animationName == "throw"){
                return false;
            }
            if(target && window.catpet.activePets[target]){
                if(
                    (_self.stats.direction == "left" && _self.stats.posLeft < window.catpet.activePets[target].stats.posLeft) ||  
                    (_self.stats.direction == "right" && _self.stats.posLeft > window.catpet.activePets[target].stats.posLeft)
                )
                    return "You're not facing the target direction";
                else if (
                    (window.catpet.activePets[target].stats.posLeft >= _self.stats.posLeft && window.catpet.activePets[target].stats.posLeft - _self.stats.posLeft > 50) ||  
                    (window.catpet.activePets[target].stats.posLeft < _self.stats.posLeft && _self.stats.posLeft - window.catpet.activePets[target].stats.posLeft > 50)              
                )
                    return "You're too far away";
                else if (
                    (_self.stats.posTop >= window.catpet.activePets[target].stats.posTop &&  _self.stats.posTop - window.catpet.activePets[target].stats.posTop > 30)
                )
                    return "You're too far away";
                else if (
                    (_self.stats.posTop < window.catpet.activePets[target].stats.posTop &&  window.catpet.activePets[target].stats.posTop - _self.stats.posTop > 30)
                )
                    return "You're too far away";
                    
                return true;
            } else if (target){
                return false;
            }
            
            this.clearAllActions();
            this.rendererContainer.zOrder = ++window.RenderManager.currzIndex;
            //this.rendererContainer.zOrder = this.rendererContainer.zIndex;
            //this.organizezIndex();
            
            if(window.catpet.isPlayground)
                _self.stats.idleType = "idle";
            
            _self.animationName = "petother";    
            
            if(window.catpet.isPetActive(_self.id)){
                //document.getElementById(window.catpet["pet-holder"] + _self.id).className = "anim petother " + _self.stats.direction;
                _self.changeAnimation(0, "pet other", true, 0);
            }
            _self.currentAnimationTimeout = setTimeout(function(){
                _self.idle({initiator: "pet-other"});
            }, 3000);
        };
        
